import request from '@/utils/request'
export function fetchList(params) {
  return request({
    url:'/stocksBase/listAll',
    method:'get',
    params:params
  })
}

export function fetchOne(params) {
  return request({
    url:'/historyKl/list',
    method:'get',
    params:params
  })
}

export function subscribeStock(params) {
  return request({
    url:'/historyKl/subscribe',
    method:'get',
    params:params
  })
}

export function unSubscribeStock(params) {
  return request({
    url:'/historyKl/unsubscribe',
    method:'get',
    params:params
  })
}




