<template>
  <div v-loading.body.lock="loading" element-loading-text="数据加载中...">
    <div class="date-picker-container">
      <el-date-picker
        v-model="dateRange"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions"
        @change="handleDateChange">
      </el-date-picker>
      <el-select
        v-model="selectedStockCode"
        placeholder="请选择股票"
        clearable
        filterable
        @change="handleStockChange"
        style="margin-left: 10px; width: 200px;">
        <el-option
          v-for="stock in stocks"
          :key="stock.code"
          :label="stock.name"
          :value="stock.code">
          <span style="float: left">{{ stock.name }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{ stock.code }}</span>
        </el-option>
      </el-select>
      <el-input
        v-model="newStockCode"
        placeholder="输入股票代码"
        style="margin-left: 10px; width: 150px;"
        :disabled="loading"
        @keyup.enter.native="handleSubscribe">
      </el-input>
      <el-button
        type="primary"
        style="margin-left: 10px;"
        @click="handleSubscribe"
        :disabled="loading || !newStockCode">
        订阅
      </el-button>
      <el-button
        type="primary"
        style="margin-left: 10px;"
        @click="handleUnSubscribe"
        :disabled="loading || !newStockCode">
        取消订阅
      </el-button>
    </div>
    <div class="chart-container" id="chart-container">
      <div
        v-for="stock in filteredStocks"
        :key="stock.code"
        class="chart"
        :id="`chart-${stock.code}`"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import {fetchList, fetchOne, subscribeStock, unSubscribeStock} from '@/api/futu';

const defaultListQuery = {
  keyword: null,
  pageNum: 1,
  pageSize: 5,
  publishStatus: null,
  verifyStatus: null,
  productSn: null,
  productCategoryId: null,
  brandId: null
};

export default {
  name: 'StockCharts',
  data() {
    const end = new Date();
    const start = new Date();
    start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
    return {
      listQuery: Object.assign({}, defaultListQuery),
      stocks: [],
      charts: [],
      dateRange: [start, end],
      selectedStockCode: '',
      newStockCode: '',
      loading: false,
      pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近两年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 730);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近三年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1095);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近五年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1825);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '全部',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              //设置 start 为 2015.1.1
              start.setTime(new Date('2015-01-01').getTime());
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      }
    };
  },
  computed: {
    filteredStocks() {
      if (this.selectedStockCode) {
        return this.stocks.filter(stock => stock.code === this.selectedStockCode);
      }
      return this.stocks;
    }
  },
  async mounted() {
    await this.fetchStocks();
    await this.createCharts();
  },
  methods: {
    async fetchStocks() {
      await fetchList(this.listQuery).then(response => {
        this.stocks = response.data;
      });
    },
    async createCharts() {
      this.charts.forEach(chart => {
        if (chart) {
          chart.dispose();
        }
      });
      this.charts = [];
      await this.$nextTick();
      for (const stock of this.filteredStocks) {
        await this.createChart(stock);
      }
      // 添加窗口 resize 监听
      this.handleResize = () => {
        this.charts.forEach(chart => {
          if (chart) {
            chart.resize();
          }
        });
      };
      window.addEventListener('resize', this.handleResize);
      console.log('All charts loaded');
    },
    async createChart(stock) {
      const containerId = `chart-${stock.code}`;
      const container = document.getElementById(containerId);
      if (!container) {
        console.warn(`Container ${containerId} not found`);
        return;
      }

      const chart = echarts.init(container);
      this.charts.push(chart);

      const [startDate, endDate] = this.dateRange || [null, null];
      const params = {
        code: stock.code,
        startDate: startDate ? startDate.toISOString().split('T')[0] : null,
        endDate: endDate ? endDate.toISOString().split('T')[0] : null
      };

      await fetchOne(params).then(response => {
        const data = response.data.historyKlList;
        const dates = data.map(item => item.dataTime);
        const candlestickData = data.map(item => [
          item.openPrice,
          item.closePrice,
          item.lowPrice,
          item.highPrice
        ]);

        const option = {
          title: {
            text: `${stock.name} (${stock.code})`,
            left: 'center'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {type: 'cross'}
          },
          xAxis: {
            type: 'category',
            data: dates,
            boundaryGap: true,
            axisLine: {onZero: false},
            splitLine: {show: false},
            min: 'dataMin',
            max: 'dataMax'
          },
          yAxis: {
            scale: true,
            splitArea: {show: true}
          },
          dataZoom: [
            {type: 'inside', start: 0, end: 100},
            {show: true, type: 'slider', top: '90%', start: 0, end: 100}
          ],
          series: [
            {
              name: 'Price',
              type: 'candlestick',
              data: candlestickData,
              itemStyle: {
                color: '#00da3c',
                color0: '#ec0000',
                borderColor: '#008F28',
                borderColor0: '#8A0000'
              },
              large: true
            }
          ]
        };
        chart.setOption(option);
      }).catch(error => {
        console.error(`Failed to fetch data for ${stock.code}:`, error);
      });
    },
    async handleSubscribe() {
      if (!this.newStockCode) {
        this.$message.error('请输入股票代码');
        return;
      }
      this.loading = true;
      try {
        const response = await subscribeStock({code: this.newStockCode});
        if (response.code === 200) {
          this.$message.success('订阅成功');
          setTimeout(async () => {
            this.loading = false;
            await this.fetchStocks(); // Refresh stock list
            await this.createCharts();
          }, 8000);
        } else {
          this.$message.error(response.message || '订阅失败');
          this.loading = false;
        }
      } catch (error) {
        console.error('Subscription error:', error);
        this.loading = false;
      }
    },
    async handleUnSubscribe() {
      if (!this.newStockCode) {
        this.$message.error('请输入股票代码');
        return;
      }
      this.loading = true;
      try {
        const response = await unSubscribeStock({code: this.newStockCode});
        if (response.code === 200) {
          this.$message.success('取消订阅成功');
          await this.fetchStocks(); // Refresh stock list
          await this.createCharts();
        } else {
          this.$message.error(response.message || '取消订阅失败');
        }
        this.loading = false;
      } catch (error) {
        console.error('Subscription error:', error);
        this.loading = false;
      }
    },
    handleDateChange() {
      this.createCharts();
    },
    handleStockChange() {
      this.$nextTick(() => {
        this.createCharts();
      });
    }
  },
  beforeDestroy() {
    this.charts.forEach(chart => {
      if (chart) {
        chart.dispose();
      }
    });
    this.charts = [];
    // 移除 resize 监听器
    window.removeEventListener('resize', this.handleResize);
  }
};
</script>
<style scoped>
html, body, #app {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* 防止水平滚动条 */
}
.date-picker-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  flex-wrap: wrap; /* 移动端允许换行 */
}

.chart-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  width: 100%; /* 确保容器占满父容器宽度 */
  box-sizing: border-box; /* 防止 padding 导致溢出 */
}

.chart {
  width: 100%;
  height: 400px; /* 默认高度 */
}

/* 移动端媒体查询 */
@media (max-width: 768px) {
  .chart-container {
    padding: 10px; /* 减少移动端内边距 */
  }

  .chart {
    height: 50vh; /* 使用视口高度的 50% */
    min-height: 200px; /* 最小高度防止过小 */
  }

  .date-picker-container {
    flex-direction: column; /* 移动端垂直排列 */
    gap: 10px; /* 控件间距 */
  }

  .el-select,
  .el-input,
  .el-button {
    width: 100% !important; /* 移动端控件占满宽度 */
    margin-left: 0; /* 移除左边距 */
    margin-top: 10px; /* 添加垂直间距 */
  }
}
</style>



